<template>
  <el-row class="tac">
  <el-col :span="24">
    <el-menu class="el-menu-vertical-demo"
      router
      unique-opened
      default-active="jobs"
      @open="handleOpen"
      @close="handleClose">
          <el-menu-item v-for="item in menu" :key="item.id" :index="item.path" v-text="item.name">
          </el-menu-item>
    </el-menu>
  </el-col>
</el-row>
</template>

<style scoped>
  .over-hide{
    overflow: hidden;
  }
</style>

<script>
  import menu from '@/config/menu-config'

  export default {
    data () {
      return {
        menu: menu
      }
    },
    methods: {
      handleOpen (key, keyPath) {
        console.log(key, keyPath)
      },
      handleClose (key, keyPath) {
        console.log(key, keyPath)
      }
    }
  }
</script>


